@charset "UTF-8";
html, body {
  height: 100%; }

.navbar, .messages {
  z-index: 1; }

.annotator__container {
  background-image: url(../images/noise.png); }

.annotator__container {
  position: absolute;
  top: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  padding-top: 52px;
  display: flex;
  flex-flow: columns; }

.confidence-label {
  font-weight: normal;
  font-size: 100%;
  line-height: inherit; }

.annotator__canvas {
  height: 100%;
  flex: 2;
  overflow: hidden;
  position: relative; }
  .annotator__canvas .confidence-control {
    position: absolute;
    right: 43px;
    z-index: 1;
    width: 51px;
    height: 28px;
    transition: width 0.25s ease;
    top: 0.5em;
    background-color: rgba(255, 255, 255, 0.4);
    padding: 4px 3px;
    border-radius: 4px;
    white-space: nowrap;
    overflow: hidden; }
    .annotator__canvas .confidence-control:hover {
      width: 200px;
      background-color: rgba(255, 255, 255, 0.6); }
    .annotator__canvas .confidence-control .label {
      padding: 0.2em 0.6em;
      border-radius: 2px; }
    .annotator__canvas .confidence-control .confidence-range {
      display: inline-block;
      width: 137px;
      margin-left: 3px;
      vertical-align: middle;
      box-sizing: content-box; }

@keyframes rotate {
  100% {
    transform: rotate(360deg); } }

.canvas__loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 50px;
  width: 50px;
  background-color: rgba(0, 0, 0, 0.75);
  border-radius: 10px;
  opacity: 0;
  pointer-events: none;
  z-index: 1;
  transition: opacity 0.25s; }
  .canvas__loader.active {
    opacity: 1; }
    .canvas__loader.active:before {
      animation: rotate 5s infinite linear; }
  .canvas__loader:before {
    content: '';
    width: 80%;
    height: 80%;
    position: absolute;
    top: 10%;
    left: 10%;
    background: url(../images/dias-wheel.png) center no-repeat;
    background-size: contain; }

.annotator__sidebar {
  background-color: #222;
  display: flex;
  flex-flow: row nowrap;
  color: #9D9D9D; }
  .annotator__sidebar .confidence-label {
    margin-right: 0.6em; }

.sidebar__permanent {
  display: flex;
  flex-flow: column nowrap; }

.sidebar__controls {
  padding-bottom: 1em; }

.sidebar__permanent, .sidebar__categories {
  width: 360px;
  border-left: 1px solid #090909;
  padding: 1em; }

.sidebar__categories {
  display: none;
  overflow-y: auto; }
  .open.sidebar__categories {
    display: block; }

.sidebar__minimap {
  background-image: url(../images/noise.png);
  height: 200px;
  margin-bottom: 1em;
  border: 1px solid #090909; }

.icon {
  min-height: 20px;
  min-width: 20px;
  display: inline-block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 20px 20px;
  vertical-align: middle; }
  .icon.btn {
    min-width: 34px;
    min-height: 34px; }
  .icon.icon-point {
    background-image: url(../images/point.svg); }
    .icon.icon-point.active {
      background-image: url(../images/point_white.svg); }
  .icon.icon-rectangle {
    background-image: url(../images/rectangle.svg); }
    .icon.icon-rectangle.active {
      background-image: url(../images/rectangle_white.svg); }
  .icon.icon-polygon {
    background-image: url(../images/polygon.svg); }
    .icon.icon-polygon.active {
      background-image: url(../images/polygon_white.svg); }
  .icon.icon-linestring {
    background-image: url(../images/linestring.svg); }
    .icon.icon-linestring.active {
      background-image: url(../images/linestring_white.svg); }
  .icon.icon-circle {
    background-image: url(../images/circle.svg); }
    .icon.icon-circle.active {
      background-image: url(../images/circle_white.svg); }

.sidebar__annotations {
  flex-grow: 2;
  overflow-y: auto; }

.annotation-list__item {
  cursor: pointer;
  user-select: none;
  padding: 1em;
  padding-right: 0;
  border-bottom: 1px solid #333;
  position: relative; }
  .annotation-list__item:first-child {
    border-top: 1px solid #333; }
  .annotation-list__item:hover, .annotation-list__item.selected {
    color: white; }
  .annotation-list__item.selected:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    border-left: 3px solid #ff5e00; }
  .annotation-list__item .icon {
    margin-right: 1em; }

.label-list {
  padding-top: 1em; }

.label-list__item {
  padding: 0.75em 0; }
  .label-list__item:first-child {
    padding-top: 0; }
  .label-list__item:last-child {
    padding-bottom: 0; }
  .label-list__item .label__user {
    color: #9D9D9D; }
  .label-list__item .close {
    color: white;
    text-shadow: none; }

.sidebar__categories.open {
  display: flex;
  flex-flow: column nowrap; }

.categories-search {
  margin-bottom: 1em; }
  .categories-search .form-control {
    display: inline-block;
    width: auto; }

.categories-body {
  flex-grow: 2;
  overflow: auto; }

.categories-list:not(:last-child) {
  border-bottom: 1px solid #333; }

.categories-list__name {
  margin-bottom: 2px; }

.categories-list__items > .label-category-item {
  padding-left: 0; }

.label-category-item {
  padding-left: 19px;
  padding-top: 0.5em;
  cursor: pointer; }
  .label-category-item .item__name {
    display: block; }
  .label-category-item > .item__name:before {
    content: '▹ '; }
  .label-category-item > .item__name:hover {
    color: white; }
  .label-category-item.expandable > .item__name:before {
    content: '▸ '; }
  .label-category-item.expandable.open > .item__name {
    color: white; }
    .label-category-item.expandable.open > .item__name:before {
      content: '▾ '; }
  .label-category-item > .label-category-subtree {
    max-height: 0;
    overflow: hidden; }
  .label-category-item.open > .label-category-subtree {
    max-height: none; }
  .label-category-item.selected > .item__name {
    color: white;
    font-weight: bold; }
